import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import  Button  from 'bee-button';
import CitySelect from '../../src';

class Demo1 extends Component {

	constructor() {
		super();
		this.state = {
			defaultValue:{ province:'',city:'',area:''},
			value:null
		}
	}

	onChange=(obj)=>{
		console.log(obj)
	}

	btnOnClick=()=>{
		this.setState({
			value:{ province:'',city:'',area:''}
		})
	}

	render () {
		let value = { province:'',city:'',area:''};
		return (
			<div>
				<CitySelect ref='city' onChange={this.onChange} defaultValue={this.state.defaultValue} value={value}/>
				<Button shape="border" onClick={this.btnOnClick} style={{marginTop:"10px"}}></Button>
			</div>
	)}
}
export default Demo1;


